<template>
  <div class="topic1">
    <div class="box-content">
      <div class="btn">
        <el-button type="primary" size="mini" @click="addUser">新增</el-button>
      </div>

      <el-table :data="currentList" border style="width: 100%">
        <el-table-column fixed prop="username" label="用户名">
        </el-table-column>
        <el-table-column prop="phone" label="手机号"> </el-table-column>
        <el-table-column prop="email" label="邮箱"> </el-table-column>
        <el-table-column prop="password" label="密码"> </el-table-column>
        <el-table-column label="生日">
          <template slot-scope="scope">
            <div>
              <span>{{ scope.row.birthday | timeFormat }}</span>
            </div>
          </template>
        </el-table-column>
        <el-table-column prop="sex" label="性别"> </el-table-column>
        <el-table-column prop="updateTime" label="更新时间"> </el-table-column>
        <el-table-column fixed="right" label="操作">
          <template slot-scope="scope">
            <el-button type="text" size="small" @click="editUser(scope.row)"
              >编辑</el-button
            >
            <el-button
              type="text"
              size="small"
              @click="delectUser(scope.row, scope.$index)"
              >删除</el-button
            >
          </template>
        </el-table-column>
      </el-table>

      <el-pagination
        style="margin-top:2vh"
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page.sync="currentPage"
        :page-size="pageSize"
        layout="total, prev, pager, next"
        :total="total"
      >
      </el-pagination>
    </div>

    <el-dialog
      :title="dialogTitle"
      :visible.sync="dialogVisible"
      v-if="dialogVisible"
      width="30%"
      @close="handleClose"
    >
      <el-form
        ref="form"
        :model="form"
        label-position="left"
        label-width="60px"
        :rules="rules"
      >
        <el-form-item label="用户名" prop="username">
          <el-input v-model="form.username"></el-input>
        </el-form-item>
        <el-form-item label="手机号" prop="phone">
          <el-input v-model="form.phone"></el-input>
        </el-form-item>
        <el-form-item label="密码">
          <el-input type="password" v-model="form.password"></el-input>
        </el-form-item>
        <el-form-item label="邮箱" prop="email">
          <el-input v-model="form.email"></el-input>
        </el-form-item>
        <el-form-item label="生日">
          <el-date-picker
            style="width: 100%;"
            v-model="form.birthday"
            type="date"
            placeholder="选择日期"
          ></el-date-picker>
        </el-form-item>
        <el-form-item label="性别">
          <el-select
            style="width: 100%;"
            v-model="form.sex"
            placeholder="请选择性别"
            clearable
            filterable
          >
            <el-option label="男" value="男"></el-option>
            <el-option label="女" value="女"></el-option>
          </el-select>
        </el-form-item>
      </el-form>

      <span slot="footer" class="dialog-footer">
        <el-button size="mini" @click="handleClose">取 消</el-button>
        <el-button size="mini" type="primary" @click="confirm">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import { userList } from "/src/assets/data.js";
import dayjs from "dayjs";
export default {
  data() {
    let customize = (rule, value, callback) => {
      let nameList = JSON.parse(localStorage.getItem("userList")).map(
        item => item.username
      );
      if (nameList.indexOf(value) !== -1) {
        callback(new Error("用户名已存在"));
      } else if (!value) {
        callback(new Error("请输入用户名"));
      } else {
        callback();
      }
    };
    let phoneVali = (rule, value, callback) => {
      const result = /^1(3|4|5|6|7|8|9)\d{9}$/;
      if (!value) {
        callback();
      } else {
        if (result.test(value)) {
          callback();
        } else {
          return callback(new Error("输入正确的手机号"));
        }
      }
    };
    let emilVali = (rule, value, callback) => {
      const result = /^\w+([-+.]\w+)*@\w+([-.]\w+)*\.\w+([-.]\w+)*$/;
      if (!value) {
        callback();
      } else {
        if (result.test(value)) {
          callback();
        } else {
          return callback(new Error("输入正确的邮箱"));
        }
      }
    };
    return {
      dialogTitle: "新增",
      tableData: [],
      currentPage: 1,
      total: 0,
      pageSize: 5,
      dialogVisible: false,
      form: {
        username: "",
        phone: "",
        password: "",
        birthday: "",
        sex: "",
        email: "",
        updateTime: ""
      },
      rules: {
        username: [{ validator: customize, trigger: "blur" }],
        phone: [{ validator: phoneVali, trigger: "blur" }],
        email: [{ validator: emilVali, trigger: "blur" }]
      }
    };
  },
  filters: {
    timeFormat(val) {
      return dayjs(val).format("YYYY-MM-DD");
    }
  },
  mounted() {
    if (!localStorage.getItem("userList")) {
      localStorage.setItem("userList", JSON.stringify(userList));
      this.tableData = userList;
    } else {
      this.tableData = JSON.parse(localStorage.getItem("userList"));
    }
    this.total = this.tableData.length;
  },
  computed: {
    beginIndex() {
      return (this.currentPage - 1) * this.pageSize;
    },
    currentList() {
      return this.tableData.slice(
        this.beginIndex,
        this.beginIndex + this.pageSize
      );
    }
  },

  methods: {
    addUser() {
      this.dialogVisible = true;
    },
    editUser(row) {
      this.dialogTitle = "编辑";
      this.dialogVisible = true;
      this.form = row;
    },
    delectUser(row, index) {
      this.tableData.splice(index + this.pageSize * (this.currentPage - 1), 1);
      this.total = this.tableData.length;
      localStorage.setItem("userList", JSON.stringify(this.tableData));
    },
    confirm() {
      this.$refs.form.validate(valid => {
        if (valid) {
          this.form.updateTime = dayjs(new Date()).format(
            "YYYY-MM-DD HH:mm:ss"
          );
          if (this.dialogTitle === "新增") {
            this.tableData.push(this.form);
          }
          this.total = this.tableData.length;

          localStorage.setItem("userList", JSON.stringify(this.tableData));
          this.dialogVisible = false;
        } else {
          this.$message.error("请检查表单");
          return false;
        }
      });
    },
    handleClose() {
      this.dialogVisible = false;
      this.form = {
        username: "",
        phone: "",
        password: "",
        birthday: "",
        sex: "",
        email: "",
        updateTime: ""
      };
    },
    handleClick(row) {
      console.log(row);
    },
    handleSizeChange(val) {
      console.log(val);
    },
    handleCurrentChange(val) {
      console.log(val);
    }
  }
};
</script>

<style lang="less" scoped>
.topic1 {
  height: 100%;
  width: 100%;

  .box-content {
    margin: 2vh 5vw;
    .btn {
      text-align: left;
      margin-bottom: 2vh;
    }
  }
}
</style>
